import React, { useRef, useCallback } from "react";
import { GET_MEDIA_NAME } from "../../../../../Services/Utils/utils";
import PinchZoomPan from "react-image-zoom-pan";
import { baseUrl } from '../../../../../Assets/AssetsHelper';
const Media = React.lazy(() => import('../../../CommonComponents/Media/Media'));
const PopUpVideo = React.lazy(() => import('../../../CommonComponents/Media/Elements/PopUpVideo'));
const PopUpAudio = React.lazy(() => import('../../../CommonComponents/Media/Elements/PopUpAudio'));

const ModalMedia = (props) => {
    const onErrorImageUpdate = event => {
        event.target.src = baseUrl(`media/images/error/image.svg`)
    }
    return <div className="assets_modal_img_outer">
        <div className="assets_modal_img_wrapper">
            {(props.modalAssetData.type == 2 || props.modalAssetData.type == 4) ?

                <PinchZoomPan maxScale={5} minScale={0.2} position={'center'} zoomButtons={false} doubleTapBehavior={'reset'}>
                    <img className="assets_modal_media__" src={props.modalAssetData.publishImage ? props.modalAssetData.publishImage : props.modalAssetData.postFile} height={props.modalAssetData.mediaHeight} width={props.modalAssetData.mediaWidth} alt={GET_MEDIA_NAME(props.modalAssetData.postFile)} onError={onErrorImageUpdate} />
                </PinchZoomPan>

                : (props.modalAssetData.type == 1) ? <Media textContent={props.modalAssetData.content} mediaType={props.modalAssetData.type == 1} rating={props.modalAssetData.rating} asset={props.modalAssetData} animation={'fade'} socialNetwork={props.modalAssetData.networkId} key={`post_image${props.modalAssetData.id}`} /> : (props.modalAssetData.type == 3 || props.modalAssetData.type == 5) ? props.modalAssetData.networkId != 12 ? <PopUpVideo data={props.modalAssetData} /> : <PopUpAudio data={props.modalAssetData} /> : null}
        </div>
    </div>

};
export default ModalMedia;